// banner
.banner {
  background-image: url(../images/bg_home_mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 336px;

  @include md {
    height: 680px;
  }

  @include lg {
    background-image: url(../images/bg_home.jpg);
    // 
    // 1.=
    // height: 680px;

    // 2.banner100vh=<nav>90px<nav>
    height: calc(100vh - 90px);
  }

  h2 {
    // ()
    background: linear-gradient(#09acf5, #62db54);
    /*  */
    background-clip: text;
    -webkit-background-clip: text;
    /*  Chrome  webkit  */
    // 
    color: transparent;

    &,
    p,
    h3 {
      display: block;
    }
  }

  .banner-text {
    padding: 32px 0;

    @include md {
      padding: 50px 0;
    }

    // banner
    .square {
      width: 10px;
      height: 10px;
      background-color: #fff;
    }
  }
}

// 
.strength {
  @include lg {
    h3 {
      text-align: left;
    }

    .mark-line {
      display: flex;
      justify-content: flex-start;
    }

    .mx-auto {
      margin-right: 0 !important;
      margin-left: 0 !important;
    }
  }

  .s-border {
    background-color: #202020;

    @include lg {
      background-color: #2D2D2D;
    }

    &:hover {
      @include lg {
        // outline1px()border()
        outline: 1px solid #62DB54;
        border-radius: 12px;
        background-color: #202020;

        span {
          background: linear-gradient(#09acf5, #62db54);
          background-clip: text;
          -webkit-background-clip: text;
          color: transparent;
        }
      }
    }
  }

  span {
    font-size: 50px;
    background: linear-gradient(#09acf5, #62db54);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;

    @include lg {
      //  color 
      color: #FFFFFF;
    }
  }
}

.mark-line {
  width: 50px;
  border-bottom: 3px solid #62DB54;
}

// 
.class-block {
  h3 {
    font-size: 22px;
  }

  .mark-line {
    margin-bottom: 50px;

    @include lg {
      margin-bottom: 74px;
    }
  }

  .class {
    margin-bottom: 50px;

    &:nth-last-child(1) {
      margin-bottom: 0px;

      @include md {
        margin-bottom: 50px;
      }
    }

    span {
      // try
      margin-left: 28px;
      padding: 0 8px;
    }
  }
}

// 
.course-block {
  .mark-line {
    margin-bottom: 26px;
  }

  .course-swiper-slide {
    // inline-block
    width: max-content !important;
    padding: 16px;

    @include md {
      padding: 24px 32px;
    }
  }
}

// 
.teacher-block {
  h3 {
    font-size: 22px;
  }

  .teacher {
    &:hover {
      outline: 2px solid #62DB54;
    }

    &-btn {
      background: linear-gradient(104deg, #09ACF5, #62DB54);
      margin: 0 auto;
    }
  }
}

// 
.class-point {
  li {
    .mark-line-class {
      width: 50px;
      border-bottom: 3px solid #62db54;

      @include lg {
        border-bottom: 3px solid #FFFFFF;
      }

      &:hover {
        @include lg {
          width: 65px;
          border-bottom: 3px solid #62DB54;
        }
      }
    }

    span {
      color: #62db54;

      @include lg {
        color: #FFFFFF;
      }
    }

    .mx-auto {
      @include lg {
        margin-right: 0 !important;
        margin-left: 0 !important;
      }
    }

    // carimgobject-fit: cover
    .card {
      // 44
      height: 263px;

      @include lg {
        height: 615px;
      }

      &-img {
        opacity: 0.1;
        object-fit: cover;
      }
    }

    &:hover {
      img {
        opacity: 0.88;
      }

      span {
        color: #62db54;
      }

      .mark-line-class {
        width: 65px;
        border-bottom: 3px solid #62DB54;
      }

      p {
        color: #FFFFFF;
        opacity: 1;
      }
    }
  }
}

// 
.share-block {
  h3 {
    font-size: 22px;
  }
  .card-corner {
    display: none;
    @include lg {
      display: block;
    }
  }
}

// 
.feedback {
  background-image: url(../images/bg_share.jpg);
  height: 733px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (max-width:991px) {
    height: auto;
  }
}

.feedback-swiper {
  //++
  max-width: 925px;
  // width: 100%;
  height: 429px;

  @media (max-width: 991px) {
    height: auto;
  }

  // 
  .swiper-img {
    max-width: 525px;
    // width: 60%;
    // height: 90%;
    // swiper-sliderelativeswiper-img absoluteswiper-slide.swiper-imgswiper-slideswiper-slide=.feedback-swipermax-width: 925pxheight: 429pxswiper-imgswiper-slide    top: 0  left: 10px
    position: absolute;
    top: 0;
    left: 10px;

    // .swiper-img
    &::after {
      content: '';
      // width:100%.swiper-img
      width: 100%;
      height: 100%;
      border: 1px solid #7c7e77;
      border-radius: 24px;
      // .swiper-img
      position: absolute;
      left: -10px;
      top: 10px;
      z-index: -1;

      @media (max-width: 991px) {
        left: -5px;
        top: 5px;
      }
    }

    img {
      // 100%100%
      width: 100%;
      height: 100%;
      // object-fix: cover (.swiper-img) background-size: cover
      object-fit: cover;
    }

    @media (max-width: 991px) {
      position: relative;
      width: calc(100% - 5px);
      height: 213px;
      left: 5px;
      margin-bottom: 40px;
    }
  }

  // 
  .swiper-card {
    max-width: 416px;
    border-radius: 24px 24px 24px 0;
    // .swiper-slide
    position: absolute;
    bottom: 0;
    right: 0;

    h4,
    p {
      color: #494755;
    }

    &-img {
      // .swiper-card
      position: absolute;
      right: 32px;
      bottom: 100%;
      transform: translateY(50%);

      @media (max-width: 991px) {
        width: 80px;
        height: 80px;
        right: 24px;
        top: -30px;
        bottom: auto;
        transform: none;
      }
    }

    @media (max-width: 991px) {
      position: relative;
      width: 100%;
      padding: 24px;
      margin-bottom: 32px;
    }
  }

  //  pagination
  // swiper
  .swiper-button-next:after,
  .swiper-button-prev:after {
    //  content  ""
    content: '';
  }

  .swiper-button-next,
  .swiper-button-prev {
    // position: unset parent  inherit  initial() swiper swiperunsetposition
    position: unset;
    width: 24px;
    height: 24px;
    margin-top: 0;
  }

  .swiper-pagination {
    position: unset !important;
    width: auto;

    .swiper-pagination-bullet {
      background-color: #62DB54;
      opacity: initial;

      &-active {
        width: 16px;
        border-radius: 100px;
      }
    }
  }

  .swiper-buttons {
    // .feedback-swiper
    position: absolute;
    bottom: 0;
    left: 30%;
    // position 50%()
    transform: translateX(-50%);
    z-index: 10;

    @media (max-width: 991px) {
      position: initial;
      transform: none;
    }
  }
}

